export default styled(AgencyPolicy)`
  .policy {
    &__header {
      display: grid;
      place-items: center;
      padding: 50px 0;
      font-weight: bolder;
    }
    &__main {
      margin: 50px auto;
      gap: 20px;
      &__package {
        padding: 50px 70px;
        border: 1px solid ${(props) => props.theme.colorSecondary};
        border-radius: 16px;
        li {
          border-top: 1px solid ${(props) => props.theme.colorSecondary};
          // border-bottom: $border;
        }
        &:nth-child(2) {
          border-width: 2px;
          background-color: ${(props) => props.theme.colorCard};
        }
      }
    }
    @media ${(props) => props.theme.breakPoints.lg} {
      &__header {
        padding: 32px 0.75rem;
      }
    }
  }
`;
